<template>
  <div class="devide" ref="devide" v-show="false">
  </div>
</template>

<script>
export default {
  props: {
    vertical: {
      type: Boolean,
      default: false
    },
    size: {
      type: Number,
      default: 5
    }
  },
  data () {
    return {
      before: null,
      after: null,
      oldPos: {}
    }
  },
  mounted () {
    this.getBrother()
  },
  beforeDestroy () {
    if (this.before) {
      this.before.removeEventListener('mousemove', this.elementMouseMove)
      this.before = null
    }
    if (this.after) {
      this.after.removeEventListener('mousemove', this.elementMouseMove)
      this.after = null
    }
  },
  methods: {
    getBrother () {
      if (!this.$refs.devide) return
      let before = this.before
      let after = this.after
      this.after = this.$refs.devide.nextSibling
      let parentNode = this.$refs.devide.parentElement
      let children = parentNode.childNodes
      for (let node of children) {
        if (node.nextSibling === this.$refs.devide) {
          this.before = node
          break
        }
      }
      if (before && before !== this.before) {
        before.removeEventListener('mousemove', this.elementMouseMove)
      }
      if (after && after !== this.after) {
        after.removeEventListener('mousemove', this.elementMouseMove)
      }
      if (this.before && this.after) {
        if (this.before !== before) {
          this.before.addEventListener('mousemove', this.elementMouseMove)
        }
        if (this.after !== after) {
          this.after.addEventListener('mousemove', this.elementMouseMove)
        }
        return true
      }
      return false
    },
    inArea (event) { // 在拖动范围内
      if (!this.getBrother()) return false
      let rectBefore = this.before.getBoundingClientRect()
      let rectAfter = this.after.getBoundingClientRect()
      let value = true
      if (this.vertical) {
        value = event.clientX <= rectBefore.right && event.clientX <= rectAfter.right
        value = value && event.clientX >= rectBefore.left && event.clientY >= rectAfter.left
        value = value && event.clientY >= rectBefore.bottom - this.size && event.clientY <= rectAfter.top + this.size
      } else {
        value = event.clientY <= rectBefore.bottom && event.clientY <= rectAfter.bottom
        value = value && event.clientY >= rectBefore.top && event.clientY >= rectAfter.top
        value = value && event.clientX >= rectBefore.right - this.size && event.clientX <= rectAfter.left + this.size
      }
      return value
    },
    elementMouseMove (event) {
      if (!this.inArea(event)) {
        if (this.before) {
          this.before.removeEventListener('mousedown', this.mouseDown)
          if (this.before.__devide) {
            if (this.before.__devide === this.$refs.devide) {
              this.before.classList.remove('__devide-move-s', '__devide-move-e')
              delete this.before.__devide
            }
          }
        }
        if (this.after) {
          this.after.removeEventListener('mousedown', this.mouseDown)
          if (this.after.__devide) {
            if (this.after.__devide === this.$refs.devide) {
              this.after.classList.remove('__devide-move-s', '__devide-move-e')
              delete this.after.__devide
            }
          }
        }
        return
      }
      this.before.__devide = this.$refs.devide
      this.after.__devide = this.$refs.devide
      let className = this.vertical ? '__devide-move-s' : '__devide-move-e'
      this.after.classList.add(className)
      this.before.classList.add(className)
      this.before.addEventListener('mousedown', this.mouseDown)
      this.after.addEventListener('mousedown', this.mouseDown)
    },
    mouseDown (event) {
      if (!this.getBrother()) return
      let rectBefore = this.before.getBoundingClientRect()
      let rectAfter = this.after.getBoundingClientRect()
      this.oldPos = {
        x: event.clientX,
        y: event.clientY,
        beforeWidth: rectBefore.width,
        beforeHeight: rectBefore.height,
        afterWidth: rectAfter.width,
        afterHeight: rectAfter.height
      }
      document.addEventListener('mousemove', this.mouseMove)
      document.addEventListener('mouseup', this.mouseUp)
      document.addEventListener('mouseleave', this.mouseLeave)
    },
    mouseMove (event) {
      if (!this.getBrother()) return
      if (this.vertical) {
        let detaY = event.clientY - this.oldPos.y
        if (detaY >= 0) {
          detaY = Math.min(this.oldPos.afterHeight, detaY)
        } else {
          detaY = -Math.min(this.oldPos.beforeHeight, -detaY)
        }
        let beforeHeight = this.oldPos.beforeHeight + detaY
        let afterHeight = this.oldPos.afterHeight + this.oldPos.beforeHeight - beforeHeight
        let oldBeforeHeight = getComputedStyle(this.before).getPropertyValue('height')
        let oldAfterHeight = getComputedStyle(this.after).getPropertyValue('height')
        this.before.style.height = beforeHeight + 'px'
        this.after.style.height = afterHeight + 'px'
        if (getComputedStyle(this.before).getPropertyValue('height') === oldBeforeHeight ||
          getComputedStyle(this.after).getPropertyValue('height') === oldAfterHeight) {
          this.before.style.width = oldBeforeHeight
          this.after.style.width = oldAfterHeight
        }
      } else {
        let detaX = event.clientX - this.oldPos.x
        if (detaX >= 0) {
          detaX = Math.min(this.oldPos.afterWidth, detaX)
        } else {
          detaX = -Math.min(this.oldPos.beforeWidth, -detaX)
        }
        let beforeWidth = this.oldPos.beforeWidth + detaX
        let afterWidth = this.oldPos.afterWidth + this.oldPos.beforeWidth - beforeWidth
        let oldBeforeWidth = getComputedStyle(this.before).getPropertyValue('width')
        let oldAfterWidth = getComputedStyle(this.after).getPropertyValue('width')
        this.before.style.width = beforeWidth + 'px'
        this.after.style.width = afterWidth + 'px'
        if (getComputedStyle(this.before).getPropertyValue('width') === oldBeforeWidth ||
          getComputedStyle(this.after).getPropertyValue('width') === oldAfterWidth) {
          this.before.style.width = oldBeforeWidth
          this.after.style.width = oldAfterWidth
        }
      }
    },
    mouseUp (event) {
      this.clearEvent()
    },
    mouseLeave (event) {
      this.clearEvent()
    },
    clearEvent () {
      if (this.before) {
        this.before.removeEventListener('mousedown', this.mouseDown)
      }
      if (this.after) {
        this.after.removeEventListener('mousedown', this.mouseDown)
      }
      document.removeEventListener('mouseup', this.mouseUp)
      document.removeEventListener('mouseleave', this.mouseLeave)
      document.removeEventListener('mousemove', this.mouseMove)
    }
  }
}
</script>

<style lang="stylus">
.__devide-move-s
  cursor s-resize !important
.__devide-move-e
  cursor e-resize !important
</style>
