<template lang="pug">
.question-page
  Spin(size="large", fix, v-if="pageLoading")
  .question-node-tree(:class="{'moving': isMove}", ref='nodeTree')
    .tree-title
      | 节点列表
    .tree-body
      nodeEditor(@current-focus="jumpTo" :projectId='projectId', :editable='true', :focusNode='schema ? schema.nodeId : -1', :expandAll='false')
  devide
  .answer-question
    .question-row(v-if='!onRecord')
      div
        Card
          .questionnaire(v-if='!onRecord')
            .project-title {{questionnaire.title}}
            .project-description
              span.description {{questionnaire.description}}
            .bottom-btn
              Button(type='primary', size='large', @click='start') 开始咨询
      .backhome
        Button(type='info', class="back-button", @click.stop="backTo('Home')") 返回主页
        Button(type='info', class="back-button", @click.stop="backTo('Page')") 返回项目页
        Button(type='info', class="back-button", @click.stop="backTo('Report')") 返回报告页
    .question-row.question-center(v-if='onRecord')
      .que(v-if='!report && this.schema')
        .questions
          Card
            .question-title
              .title {{ schema.title }}
              .description(v-if="schema.description") ({{ schema.description }})
            template(v-for='(key, index) in schema.properties')
              Additional(v-if='key.additional', :title='key.title', v-model='key.additionalShow', :key='index', :additionalKey='index', @commit='Commit', @have='AdditionalDown')
                template(v-for='(k, inx) in key.properties')
                  schemaComponent(:name='getComponentName(k)', :propsObject='k', :schemaKey="index + '-----' + inx", @input='Input', @commit='Commit', @additional='Additional', :key='inx')
              MultiNest(v-else-if='key.uuid === "00000000-0000-0000-0000-000000000012" && key.multiKey', :properties="schema.properties", :answers="answers", :title='key.title', :schemaKey='index', :key='index', :multiKey='key.multiKey', @additional='Additional')
              schemaComponent(v-else='', :name='getComponentName(key)', :propsObject='key', :schemaKey='index', @input='Input', @commit='Commit', @additional='Additional', :key='index', class="answer-area")
        .backhome
          Button(type='info', class="back-button", @click.stop='answer(2)') 上一题
          Button(type='info', class="back-button", @click.stop='reset') 重做
      .report(v-else='')
        .report-title 律品法律咨询意见书
        .report-content(v-html="reportContent ? reportContent : '咨询报告为空'")
        .backhome
          Button(type='info', class="back-button", @click.stop="backTo('Home')") 返回主页
          Button(type='info', class="back-button", @click.stop="backTo('Page')") 返回项目页
          Button(type='info', class="back-button", @click.stop="backTo('Report')") 返回报告页
          Button(type='info', class="back-button", @click.stop='reportAnswer(2)') 上一题
          Button(type='info', class="back-button", @click.stop='reset') 重做
  devide
  .answer-record
    .answer-record-title 做题记录
    Tree(:treeData="answerRecord", class="answer-record-tree" @node-double-click="handleRecordClick" :nodeClass="'tree-node-container'" :expand="false")
      .node-render-content(slot-scope='{ data, parentData }', :class="{ 'root-node-content': parentData.isRoot }" :title="'双击可以重做该题'")
        span(v-if="parentData.isRoot", class="root-node-title") {{ parentData.children.findIndex(item => item.id === data.id) + 1 + '. '}}
        | {{ data.title }}
</template>

<script>
import question from '@/api/question'
import Additional from '@components/additional'
import MultiNest from '@components/multiNest'
import schemaComponent from '@components/schemaComponent'
import nodeEditor from '@/components/node-editor'
import uuidv4 from 'uuid/v4'
import Tree from '@/components/simple-tree'
import showdown from 'showdown'
import devide from '@/components/devide'

export default {
  name: 'question-page',
  components: {
    schemaComponent,
    nodeEditor,
    Additional,
    MultiNest,
    Tree,
    devide
  },
  data () {
    return {
      pageLoading: true,
      schema: null,
      schemaBack: null,
      schemaString: null,
      compoentMap: null,
      onRecord: false,
      questionnaire: {},
      answers: {},
      recordId: null,
      report: false,
      reportContent: null,
      projectId: '3001',
      lastPos: {
        innerWidth: 0,
        innerHeight: 0,
        x: 0,
        y: 0
      },
      isMove: false,
      additionalKeys: [],
      answerRecord: [],
      loading: false,
      converter: null
    }
  },
  computed: {
    token () {
      return this.$store.state.user.token
    }
  },
  async created () {
    if (!this.converter) {
      this.converter = new showdown.Converter({ literalMidWordUnderscores: true })
    }
    this.projectId = this.$route.query.projectId || '1'
    this.rootId = this.$route.query.rootId
    if (this.$route.query.reset == 1) { // eslint-disable-line
      const xAccessToken = uuidv4()
      this.updateQuery('xAccessToken', xAccessToken)
      this.$request.defaults.headers.common['x-access-token'] = xAccessToken
      this.updateQuery('onRecord', '0')
      this.updateQuery('reset')
    } else {
      this.$request.defaults.headers.common['x-access-token'] = this.$route.query.xAccessToken
    }

    if (this.$route.query.onRecord === '3') {
      await this.getReport()
      this.requestAnswerRecord()
    } else if (this.$route.query.onRecord === '1') {
      await this.answer(1)
    } else {
      await this.answer(null, '', '')
    }

    this.$bus.$on('get-root-project', (callback) => callback(this.rootId))
    // this.questionnaire = await this.getQuestionnaire(projectId)

    // 如果有正在做题
    // if (window.sessionStorage.recordId && this.questionnaire.recordId === parseInt(window.sessionStorage.recordId) && window.sessionStorage.schema && window.sessionStorage.valueJson) {
    //   this.recordId = parseInt(this.questionnaire.recordId)
    //   await this.answer(1, JSON.parse(window.sessionStorage.schema), window.sessionStorage.valueJson)
    // }
  },
  beforeDestroy () {
    this.$bus.$off('get-root-project')
  },
  methods: {
    updateQuery (key, value, push = false) {
      if (value === null) {
        delete this.$route.query[key]
      } else {
        this.$route.query[key] = value
      }

      if (push) {
        window.history.replaceState(null, null, window.location.origin + window.location.hash.substring(0, window.location.hash.indexOf('?')) + '?' + this.$qs.stringify(this.$route.query))
      } else {
        window.history.pushState(null, null, window.location.origin + window.location.hash.substring(0, window.location.hash.indexOf('?')) + '?' + this.$qs.stringify(this.$route.query))
      }
    },
    // validJsonSchema () {
    //   let validate = ajv.compile(this.schema)
    //   let valid = validate({})
    //   if (!valid) console.log(validate.errors)
    // },
    // async getQuestionnaire (projectId) {
    //   return question.getQuestionnaire.apply(this, [projectId])
    // },
    // check
    jumpTo (e) {
      console.log('jump', e)
      this.$nextTick(() => {
        this.searchNodeEl = document.querySelector(`#node-title-${e.params.node.uuid}`)
        const nodePanelEl = document.querySelector(`#node-${e.params.node.id}`)
        const nodeScrollPage = this.$refs.nodeTree
        if (nodePanelEl) {
          const scrollY = nodePanelEl.getBoundingClientRect().top - nodeScrollPage.getBoundingClientRect().top
          if (scrollY > nodeScrollPage.scrollTop) {
            nodeScrollPage.scrollTo({
              top: scrollY,
              behavior: 'smooth'
            })
          }
        }
      })
    },
    answerHandler (answer, schema) {
      const uuids = ['00000000-0000-0000-0000-000000000013', '00000000-0000-0000-0000-000000000005', '00000000-0000-0000-0000-000000000007'] // 判断、单选、多选
      let r = this.deepCopy(answer)
      for (const key in schema.properties) {
        if (uuids.includes(schema.properties[key].uuid)) {
          for (const n in schema.properties[key].properties) { // TODO: 一堆脏代码
            // console.log(answer[key])
            // console.log(schema.properties[key])
            if (!answer[key].includes(n) && schema.properties[key].properties[n].additionalKeys && schema.properties[key].properties[n].additionalKeys.length) { // eslint-disable-line
              // const addTmpKey = schema.properties[key].properties[n].additionalKeys[0]
              // console.log(schema.properties[addTmpKey])
              // console.log(Object.keys(schema.properties[addTmpKey].properties)[0])
              console.log(Object.keys(schema.properties[schema.properties[key].properties[n].additionalKeys[0]].properties)[0])
              delete r[Object.keys(schema.properties[schema.properties[key].properties[n].additionalKeys[0]].properties)[0]]
              console.log(r)
            }
          }
        }
      }
      return JSON.stringify(r)
    },
    reset () {
      const xAccessToken = uuidv4()
      this.updateQuery('xAccessToken', xAccessToken)
      this.$request.defaults.headers.common['x-access-token'] = xAccessToken
      this.updateQuery('onRecord', '0')
      this.answer(null, 0, 0)
    },
    async answer (action = 1, schema, valueJson, jumpToQuestion) {
      if (this.loading) return
      this.loading = true
      this.schemaBack = null
      if (this.answers.button === null) {
        delete this.answers.button
      }
      let data = await question.answer.apply(this, [{
        projectId: this.projectId,
        action, // 1 为下一题，2 为上一题或者跳到做题记录某一题
        targetId: jumpToQuestion || null, // 跳转到某题目的记录id
        schema: schema === 0 ? null : (action === 2 ? null : this.schema), // 原样返回json模式
        valueJson: valueJson === 0 ? null : (action === 2 || JSON.stringify(this.answers) === '{}' ? null : this.answerHandler(this.answers, this.schema)) // 做题得到的结果
      }])
      if (data) {
        if (data.status === 1) {
          console.log(data)
          // 1返回的是开始咨询界面的json模式,2:联系方式界面json模式,3:做题界面的json模式
          if (data.result.uiType === 1) {
            this.updateQuery('onRecord', '0')
            this.questionnaire = data.result.schema
            this.report = false
            this.onRecord = false
            this.loading = false
          } else if (data.result.uiType === 2) {
            this.updateQuery('onRecord', '1')
            let schema = data.result.schema
            this.additionalKeys = null
            this.schemaString = JSON.stringify(schema)
            this.schema = schema
            // const valueJson = JSON.stringify(this.answers)
            this.answers = this.JsonSchemaParse(this.schema)
            this.report = false
            if (schema.properties === null) {
              this.onRecord = false
            } else {
              this.onRecord = true
              // window.sessionStorage.valueJson = valueJson
              // window.sessionStorage.recordId = this.recordId
              // window.sessionStorage.schema = JSON.stringify(this.schema)
            }
            this.loading = false
          } else if (data.result.uiType === 3) { // TODO: 联系方式界面
            this.updateQuery('onRecord', '1')
            console.log(data)
            await this.getReport()
            this.loading = false
            // window.sessionStorage.removeItem('valueJson')
            // window.sessionStorage.removeItem('recordId')
            // window.sessionStorage.removeItem('schema')
          }
        } else {
          this.$Message.error(data.msg || '未知错误')
          this.loading = false
        }
        this.requestAnswerRecord()
      } else {
        this.$Message.error('发生错误，请稍后再试')
        this.loading = false
      }
      this.pageLoading = false
    },
    async getReport () {
      let reportData = await question.getReport.apply(this, [
        {
          projectId: this.projectId
        }
      ])
      if (reportData.status === 1) {
        console.log(reportData)
        this.reportContent = this.converter.makeHtml(reportData.result.remark)
        this.report = true
        this.onRecord = true
        this.schemaBack = JSON.stringify(this.schema)
        this.schema = null // 防止做题高亮显示在最后一题
        this.updateQuery('onRecord', '3')
      } else {
        this.$Message.error(reportData.msg)
      }
      this.pageLoading = false
    },
    reportAnswer (val) {
      this.schema = JSON.parse(this.schemaBack)
      this.answer(val)
    },
    typeToValue (type) {
      switch (type) {
        case 'number':
          return null
        case 'string':
          return ''
        case 'boolean':
          return false
        case 'array':
          return []
        case 'object':
          return {}
        case 'whitespace':
          return ' '
        case 'null':
          return null
        default:
          return null
      }
    },
    JsonSchemaParse (json) {
      let tmp = {}
      for (const key in json.properties) {
        if (json.properties[key].additional) { // 追加题平铺
          for (const k in json.properties[key].properties) {
            tmp[k] = this.typeToValue(json.properties[key]['properties'][k]['type'])
          }
        } else {
          // if (!(json.properties[key].uuid === '00000000-0000-0000-0000-000000000012' && json.properties[key].multiKey) && json.properties[key].uuid !== '00000000-0000-0000-0000-000000000024') {
          if (!(json.properties[key].uuid === '00000000-0000-0000-0000-000000000012' && json.properties[key].multiKey)) {
            tmp[key] = this.typeToValue(json.properties[key]['type'])
          }
        }
      }
      console.log(tmp)
      return tmp
    },
    async start () {
      await this.answer(1, 0, 0)
    },
    getComponentName (key) {
      return key.uuid
    },
    Input (val) {
      console.log(val.schemaKey)
      // this.$set(this.values, Object.keys(val)[0], val[Object.keys(val)[0]])
      if (val.schemaKey.indexOf('-----') !== -1) {
        // if (this.answers[val.schemaKey.split('-----')[0]]) {
        //   this.$set(this.answers[val.schemaKey.split('-----')[0]], val.schemaKey.split('-----')[1], val.value)
        // } else {
        //   this.$set(this.answers, val.schemaKey.split('-----')[0], {})
        //   this.$set(this.answers[val.schemaKey.split('-----')[0]], val.schemaKey.split('-----')[1], val.value)
        // }
        this.$set(this.answers, val.schemaKey.split('-----')[1], val.value)
      } else {
        this.$set(this.answers, val.schemaKey, val.value)
      }
      console.log(this.answers)
    },
    Commit (val) {
      if (val.value) {
        this.$set(this.answers, val.schemaKey, val.value)
      }
      // console.log(val.additionalKeys)
      if (this.additionalKeys && this.additionalKeys.length) {
        this.schema.properties[this.additionalKeys[0]].additionalShow = true
      } else {
        this.answer()
      }
    },
    Additional (val) {
      if (val.additionalKeys && val.additionalKeys.length) {
        // if (!this.additionalKeys || (this.additionalKeys && this.additionalKeys.length === 0)) {
        this.additionalKeys = this.deepCopy(val.additionalKeys)
        // }

        if (val.allowClose) { // 特殊用法，请勿效仿
          this.schema.properties[this.additionalKeys[0]].additionalShow = true
        } else {
          this.schema.properties[this.additionalKeys[0]].additionalShow = 2
        }
      }
    },
    AdditionalDown (key) {
      for (const n in this.additionalKeys) {
        if (this.additionalKeys[n] === key) {
          this.additionalKeys.splice(n, 1)
          if (this.additionalKeys && this.additionalKeys.length) {
            this.schema.properties[this.additionalKeys[0]].additionalShow = true
          }
          return
        }
      }
    },
    backTo (name) {
      if (name === 'Report') {
        this.$router.push({ name, query: { projectId: (this.$route.query.projectId || '3001'), rootId: (this.$route.query.rootId || '3001') } })
      } else if (name === 'Page') {
        this.$router.push({ name, params: { projectId: this.$route.query.rootId } })
      } else {
        this.$router.push({ name, query: { projectId: (this.$route.query.rootId || '3001') } })
      }
    },
    nodeTreeMouseDown (e) {
      this.lastPos = {
        innerWidth: window.innerWidth,
        innerHeight: window.innerWidth,
        x: e.pageX,
        y: e.pageY
      }
      this.isMove = true
      window.addEventListener('mousemove', this.nodeTreeMouseMove)
      window.addEventListener('mouseup', this.nodeTreeMouseUp)
    },
    nodeTreeMouseMove (e) {
      if (this.isMove) {
        const left = this.$refs.nodeTree.offsetLeft
        const top = this.$refs.nodeTree.offsetTop
        const height = this.$refs.nodeTree.clientHeight
        const width = this.$refs.nodeTree.clientWidth

        const moveX = e.pageX - this.lastPos.x
        const moveY = e.pageY - this.lastPos.y

        this.$refs.nodeTree.style.left = `${left + moveX <= 0 || left + moveX >= this.lastPos.innerWidth - width ? left : left + moveX}px`
        this.$refs.nodeTree.style.top = `${top + moveY <= 60 || top + moveY >= this.lastPos.innerHeight - height ? top : top + moveY}px`

        this.lastPos.x = e.pageX
        this.lastPos.y = e.pageY
      }
    },
    nodeTreeMouseUp (e) {
      this.isMove = false
      window.removeEventListener('mousemove', this.nodeTreeMouseMove)
      window.removeEventListener('mouseup', this.nodeTreeMouseUp)
    },
    async requestAnswerRecord () {
      let params = {
        projectId: this.projectId,
        recordId: this.$route.query.onRecord,
        token: this.$route.query['xAccessToken']
      }
      let data = await question.getAnswerRecord.apply(this, [
        params
      ])
      if (data) {
        this.answerRecord = data
      } else {
        this.$Message.error('获取做题记录失败')
      }
    },
    handleRecordClick (event, vNode) {
      event.preventDefault()
      if (vNode.parentData.isRoot) {
        event.stopPropagation()
        let recordId = vNode.nodeData.id
        this.answer(2, '', '', recordId)
        vNode.$el.animate([
          { background: '#D0DEF8' },
          { background: '#D0DEF8' },
          { background: 'white' }
        ], {
          duration: 500
        })
      }
    }
  }
}
</script>

<style lang="stylus">
.question-page
  position relative
  background-color #f5f7f9
  display flex
  align-items stretch
  overflow auto
  .answer-question
    flex auto
    background white
    box-shadow 0 0 1px 1px #B0C8F4 inset
    border-radius 4px
    .question-row
      height 100%
      max-width 960px
      margin 0 auto
      text-align left
      position relative
      padding 80px 80px 70px
      .que
        height 100%
        display flex
        flex-direction column
        flex-wrap nowrap

      .questions
        text-align left
        // overflow auto
        .ivu-card
          height 100%
        .question-title
          display flex
          text-align left
          margin-bottom .5rem
          .title
            margin-right .5rem
            font-size 1rem
            font-weight 800
          .description
            font-size 1rem
            font-weight 500
            color #A9A9A9
        .answer-area
          padding-left 1rem

      .backhome
        margin-top 1rem
        text-align center
        .back-button
          margin 2px
      .report
        position absolute
        top 0
        bottom 0
        left 0
        right 0
        text-align left
        padding 1rem
        overflow auto
        .report-title
          text-align center
          font-size 1.5rem
          margin-bottom 1rem
        .report-content
          padding 1rem
          box-shadow 0 0 1px 1px #C9C9C9
          &:hover
            box-shadow 0 0 1px 1px #9B9B9B
    .questionnaire
      text-align center
      .project-title
        font-size 1.5rem
        font-weight bold
      .project-description
        padding 1rem
        .description
          display inline-block
          text-indent 2rem
          text-align left
      .bottom-btn
        text-align center
        .start
          border-radius 2px
    .questionnaire, .question-center
      // min-height 500px
  .answer-record
    width 25%
    min-width 250px
    overflow auto
    padding-top 2.5rem
    position relative
    background white
    .answer-record-title
      user-select none
      background #F7F7F7
      position absolute
      width 100%
      top 0
      left 0
      font-size 1.5rem
      text-align center
    .answer-record-tree
      .root-node-content
        font-size 1rem
        font-weight bold
      >.tree-node-container
        margin 0 2px
        margin-bottom 0.5rem
        padding 0.5rem
        box-shadow 0 0 2px 1px #A1BFFC
        border-radius 4px
  .question-node-tree
    width 25%
    min-width 250px
    background #F7F7F7
    overflow scroll
    text-align center
    .tree-title
      user-select none
      font-size 1.5rem
      padding 0.2rem 0.5rem
      border-bottom 1px solid rgba(0, 0, 0, 0.06)
      user-select none
    .tree-body
      padding 0.2rem
</style>

<style lang="stylus">
// 报告部分的样式，应与测试一致
.question-row .report .report-content
  word-break break-all
  pre
    white-space pre-wrap
  strong
    color #c0392b
</style>
